.rc-tour-guide .rc-tour-backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transition: all 0.3s ease-out;
  z-index: 999996;
  background-color: black;
  opacity: .8;
  background: radial-gradient(center, ellipse cover, rgba(0, 0, 0, 0.4) 0, rgba(0, 0, 0, 0.9) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#66000000', endColorstr='#e6000000', GradientType=1);
  filter: "alpha(opacity=50)";
}
.rc-tour-guide .rc-tour-helper {
  box-sizing: initial;
  z-index: 999999;
  background-color: transparent;
  cursor: default;
  border-radius: 4px;
}
.rc-tour-guide .rc-tour-mask {
  box-sizing: initial;
  border: 1px solid #777;
  border: 1px solid rgba(0, 0, 0, 0.5);
  border-radius: 4px;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.4);
  transition: all 0.3s ease-out;
  z-index: 999997;
  background-color: white;
  background-color: rgba(255, 255, 255, 0.6);
}
.rc-tour-guide .rc-tour-tooltip {
  z-index: 1000000;
  visibility: visible;
  width: 250px;
  background-color: white;
  color: #545454;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 8px;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.4);
  font-size: 0.9rem;
}
.rc-tour-guide .rc-tour-tooltip p {
  margin: 5px 3px;
}
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow {
  border: 5px solid white;
  content: '';
  position: absolute;
}
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.top-left,
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.topLeft {
  bottom: -10px;
  border-top-color: white;
  border-right-color: transparent;
  border-bottom-color: transparent;
  border-left-color: transparent;
}
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.top-center,
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.top,
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.topCenter {
  bottom: -10px;
  left: 50%;
  border-top-color: white;
  border-right-color: transparent;
  border-bottom-color: transparent;
  border-left-color: transparent;
}
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.top-right,
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.topRight {
  bottom: -10px;
  right: 10px;
  border-top-color: white;
  border-right-color: transparent;
  border-bottom-color: transparent;
  border-left-color: transparent;
}
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.right-top,
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.rightTop {
  left: -10px;
  top: 10px;
  border-top-color: transparent;
  border-right-color: white;
  border-bottom-color: transparent;
  border-left-color: transparent;
}
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.right-middle,
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.right,
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.rightMiddle {
  left: -10px;
  top: 45%;
  border-top-color: transparent;
  border-right-color: white;
  border-bottom-color: transparent;
  border-left-color: transparent;
}
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.right-bottom,
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.rightBottom {
  left: -10px;
  bottom: 10px;
  border-top-color: transparent;
  border-right-color: white;
  border-bottom-color: transparent;
  border-left-color: transparent;
}
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.bottom-left,
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.bottomLeft {
  top: -10px;
  border-top-color: transparent;
  border-right-color: transparent;
  border-bottom-color: white;
  border-left-color: transparent;
}
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.bottom-center,
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.bottom,
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.bottomCenter {
  top: -10px;
  left: 50%;
  margin-left: -5px;
  border-top-color: transparent;
  border-right-color: transparent;
  border-bottom-color: white;
  border-left-color: transparent;
}
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.bottom-right,
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.bottomRight {
  top: -10px;
  right: 10px;
  margin-left: -5px;
  border-top-color: transparent;
  border-right-color: transparent;
  border-bottom-color: white;
  border-left-color: transparent;
}
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.left-top,
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.leftTop {
  right: -10px;
  top: 10px;
  border-top-color: transparent;
  border-right-color: transparent;
  border-bottom-color: transparent;
  border-left-color: white;
}
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.left-middle,
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.left,
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.leftMiddle {
  right: -10px;
  top: 45%;
  border-top-color: transparent;
  border-right-color: transparent;
  border-bottom-color: transparent;
  border-left-color: white;
}
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.left-bottom,
.rc-tour-guide .rc-tour-tooltip .rc-tour-arrow.leftBottom {
  bottom: 10px;
  right: -10px;
  border-top-color: transparent;
  border-right-color: transparent;
  border-bottom-color: transparent;
  border-left-color: white;
}
.rc-tour-guide .rc-tour-tooltip .rc-tour-buttons {
  text-align: right;
}
.rc-tour-guide .rc-tour-tooltip .rc-tour-buttons > button {
  color: #666;
  background-color: white;
  border: 1px solid #666;
  border-radius: 4px;
  margin: 0 2px;
  outline: none;
  font-size: 0.9rem;
  padding: 0 4px;
}
.rc-tour-guide .rc-tour-tooltip .rc-tour-buttons > button.tour-done-btn {
  background: #2baee9;
  border: 1px solid #2baee9;
  color: #eee;
}
.rc-tour-guide .rc-tour-tooltip .rc-tour-buttons > button:hover {
  border-color: #57c5f7;
  color: #57c5f7;
}
.rc-tour-guide .rc-tour-tooltip .rc-tour-buttons > button:active {
  background-color: #57c5f7;
  color: white;
}
.rc-tour-guide-target,
tr.rc-tour-guide-target > th,
tr.rc-tour-guide-target > td {
  z-index: 999998 !important;
}
.rc-tour-guide-relative {
  position: relative;
}



